/** Humbrger menu styling ******************************************************/
.hamburger-lines {
    float: right;
    height: 1.75rem;
    width: 2.1875rem;
    position: relative;
    z-index: 2;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.hamburger-lines .line {
    display: block;
    height: 0.25rem;
    width: 100%;
    border-radius: 0.625rem;
    background: #bd6f34;
}
.hamburger-lines .line1 {
    transform-origin: 0 0;
    transition: transform 0.3s ease-in-out;
}
.hamburger-lines .line2 {
    transition: transform 0.1s ease-in-out;
}
.hamburger-lines .line3 {
    transform-origin: 0 100%;
    transition: transform 0.3s ease-in-out;
}
.header_menu-wrap input[type='checkbox'],
.filters .tab_wrap input[type='checkbox'] {
    position: absolute;
    display: block;
    height: 2rem;
    width: 1.875rem;
    top: 0;
    right: 0;
    z-index: 5;
    opacity: 0;
    cursor: pointer;
}
.header_menu-wrap input[type='checkbox']:checked ~ .hamburger-lines .line1,
.filters .tab_wrap input[type='checkbox']:checked ~ .hamburger-lines .line1 {
    transform: rotate(45deg);
}
.header_menu-wrap input[type='checkbox']:checked ~ .hamburger-lines .line2,
.filters .tab_wrap input[type='checkbox']:checked ~ .hamburger-lines .line2 {
    transform: scaleY(0);
}
.header_menu-wrap input[type='checkbox']:checked ~ .hamburger-lines .line3,
.filters .tab_wrap input[type='checkbox']:checked ~ .hamburger-lines .line3 {
    transform: rotate(-45deg);
}
